<script>
import CountTo from "vue-count-to";
import DateCountLine from "@/views/components/dateCountLine";
import TerminalPie from "@/views/components/terminalPie";
import ChinaMap from "@/views/components/chinaMap";
import OsPie from "@/views/components/osPie";

import visitApi from "@/api/visitApi";
export default {
  components: { CountTo, DateCountLine, TerminalPie, ChinaMap, OsPie },
  data() {
    return {
      countVisitData: {
        totalPV: 0,
        totalUV: 0,
        todayPV: 0,
        todayUV: 0
      },
      beforeVisitData: {
        totalPV: 0,
        totalUV: 0,
        todayPV: 0,
        todayUV: 0
      }
    };
  },
  mounted() {
    this.getCountVisit();
    let that = this;
    let interval = setInterval(() => {
      this.getCountVisit();
      if(that.$route.path != '/index'){
        clearInterval(interval)
      }
    }, 5000);
  },
  methods: {
    getCountVisit() {
      visitApi.countVisit().then(res => {
        let countVisitData = res.data;
        this.beforeVisitData = this.countVisitData;
        this.countVisitData = countVisitData;
      });
    }
  }
};
</script>

